"use client";

import Link from "next/link";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import './globals.css';
import { userService, User } from '@/lib/supabase2';
import { userService, User } from '@/lib/auth-service';

// –ò–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å—Ç–∏–ª–∏
import {
  successContainerStyle,
  successCardStyle,
  successTitleStyle,
  successTextStyle,
  successSubTextStyle,
  redirectInfoStyle,
  spinnerStyle,
  linksContainerStyle,
  primaryLinkStyle,
  backLinkStyle,
  dashboardLinkStyle,
  formContainerStyle,
  formStyle,
  inputGroupStyle,
  inputStyle,
  submitButtonStyle,
  existingUsersStyle,
  dataContainerStyle,
  usersListStyle,
  userCardStyle,
  userInfoStyle,
  deleteButtonStyle
} from './styles';

export default function Home() {
  const [users, setUsers] = useState<User[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();
  
  // –î–∞–Ω–Ω—ã–µ —Ñ–æ—Ä–º—ã
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  
  // –î–∞–Ω–Ω—ã–µ –¥–ª—è –≤—Ö–æ–¥–∞
  const [loginEmail, setLoginEmail] = useState('');
  const [loginPassword, setLoginPassword] = useState('');
  
  // –°–æ—Å—Ç–æ—è–Ω–∏—è
  const [isRegistered, setIsRegistered] = useState(false);
  const [registeredUser, setRegisteredUser] = useState('');
  const [showLogin, setShowLogin] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [currentUser, setCurrentUser] = useState<User | null>(null);

  // –ü—Ä–æ–≤–µ—Ä—è–µ–º –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ
  useEffect(() => {
    checkAuth();
    loadUsers();
  }, []);

  const checkAuth = async () => {
    const user = await userService.getCurrentUser();
    setCurrentUser(user);
  };

  const loadUsers = async () => {
    setIsLoading(true);
    try {
      const data = await userService.getUsers();
      setUsers(data);
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π:', error);
    }
    setIsLoading(false);
  };

  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // –í–∞–ª–∏–¥–∞—Ü–∏—è
    if (!name.trim() || !email.trim() || !password.trim()) {
      alert('–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è');
      return;
    }
    
    if (password !== confirmPassword) {
      alert('–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç');
      return;
    }
    
    if (password.length < 6) {
      alert('–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –Ω–µ –º–µ–Ω–µ–µ 6 —Å–∏–º–≤–æ–ª–æ–≤');
      return;
    }

    const newUser = await userService.register(name, email, password);
    if (newUser) {
      setRegisteredUser(newUser.name);
      setIsRegistered(true);
      setCurrentUser(newUser);
      resetForm();
      loadUsers();
      
      setTimeout(() => {
        router.push('/dashboard');
      }, 2000);
    }
  };

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!loginEmail.trim() || !loginPassword.trim()) {
      alert('–í–≤–µ–¥–∏—Ç–µ email –∏ –ø–∞—Ä–æ–ª—å');
      return;
    }

    const user = await userService.login(loginEmail, loginPassword);
    if (user) {
      setRegisteredUser(user.name);
      setIsRegistered(true);
      setCurrentUser(user);
      setLoginEmail('');
      setLoginPassword('');
      
      setTimeout(() => {
        router.push('/dashboard');
      }, 1000);
    }
  };

  const handleLogout = async () => {
    await userService.logout();
    setCurrentUser(null);
    setRegisteredUser('');
    setIsRegistered(false);
    alert('–í—ã –≤—ã—à–ª–∏ –∏–∑ —Å–∏—Å—Ç–µ–º—ã');
  };

  const handleDeleteUser = async (id: string) => {
    if (confirm('–£–¥–∞–ª–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è?')) {
      const success = await userService.deleteUser(id);
      if (success) {
        loadUsers();
      }
    }
  };

  const resetForm = () => {
    setName('');
    setEmail('');
    setPassword('');
    setConfirmPassword('');
    setLoginEmail('');
    setLoginPassword('');
  };

  // –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω
  if (isRegistered) {
    return (
      <>
        <header className="header"></header>
        <main className="main">
          <div style={successContainerStyle}>
            <div style={successCardStyle}>
              <h1 style={successTitleStyle}>üéâ –î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å, {registeredUser}!</h1>
              <p style={successTextStyle}>
                –í—ã —É—Å–ø–µ—à–Ω–æ {showLogin ? '–≤–æ—à–ª–∏ –≤ —Å–∏—Å—Ç–µ–º—É' : '–∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã'}!
              </p>
              <p style={successSubTextStyle}>
                –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —á–µ—Ä–µ–∑ 2 —Å–µ–∫—É–Ω–¥—ã...
              </p>
              
              <div style={redirectInfoStyle}>
                <div style={spinnerStyle}>‚è≥</div>
                <p>–ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ...</p>
              </div>
              
              <div style={linksContainerStyle}>
                <Link href="/dashboard" style={primaryLinkStyle}>
                  üìä –õ–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç
                </Link>
                <Link href="/blog" style={{
                  ...primaryLinkStyle,
                  backgroundColor: '#4a6fa5',
                  marginTop: '10px'
                }}>
                  üìù –ë–ª–æ–≥ —Å–æ–æ–±—â–µ—Å—Ç–≤–∞
                </Link>
                <button 
                  onClick={handleLogout}
                  style={{
                    ...backLinkStyle,
                    background: '#dc3545',
                    border: 'none',
                    cursor: 'pointer',
                    marginTop: '10px'
                  }}
                >
                  –í—ã–π—Ç–∏ –∏–∑ —Å–∏—Å—Ç–µ–º—ã
                </button>
              </div>
            </div>
          </div>
        </main>
        <footer className="footer">
          {showLogin ? '–í—Ö–æ–¥ –≤—ã–ø–æ–ª–Ω–µ–Ω' : '–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è'} –∑–∞–≤–µ—Ä—à–µ–Ω–∞ —É—Å–ø–µ—à–Ω–æ
        </footer>
      </>
    );
  }

  return (
    <>
      <header className="header"></header>
      <main className="main">
        
        {/* –ï—Å–ª–∏ —É–∂–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω */}
        {currentUser && (
          <div style={{
            maxWidth: '500px',
            margin: '0 auto 30px',
            padding: '20px',
            background: '#e8f5e8',
            borderRadius: '10px',
            textAlign: 'center'
          }}>
            <p>–í—ã –≤–æ—à–ª–∏ –∫–∞–∫: <strong>{currentUser.name}</strong></p>
            <div style={{ display: 'flex', gap: '10px', justifyContent: 'center', marginTop: '10px' }}>
              <Link href="/dashboard" style={{
                padding: '8px 16px',
                background: '#08572f',
                color: 'white',
                textDecoration: 'none',
                borderRadius: '5px'
              }}>
                –õ–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç
              </Link>
              <button 
                onClick={handleLogout}
                style={{
                  padding: '8px 16px',
                  background: '#dc3545',
                  color: 'white',
                  border: 'none',
                  borderRadius: '5px',
                  cursor: 'pointer'
                }}
              >
                –í—ã–π—Ç–∏
              </button>
            </div>
          </div>
        )}

        {/* –§–æ—Ä–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏/–≤—Ö–æ–¥–∞ */}
        <div style={formContainerStyle}>
          <h2 style={{ textAlign: 'center', color: '#08572f', marginBottom: '20px' }}>
            {showLogin ? '–í—Ö–æ–¥ –≤ —Å–∏—Å—Ç–µ–º—É' : '–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è'}
          </h2>
          
          {showLogin ? (
            // –§–æ—Ä–º–∞ –≤—Ö–æ–¥–∞
            <form onSubmit={handleLogin} style={formStyle}>
              <div style={inputGroupStyle}>
                <input
                  type="email"
                  placeholder="Email"
                  value={loginEmail}
                  onChange={(e) => setLoginEmail(e.target.value)}
                  style={inputStyle}
                  required
                />
              </div>
              <div style={inputGroupStyle}>
                <div style={{ position: 'relative' }}>
                  <input
                    type={showPassword ? "text" : "password"}
                    placeholder="–ü–∞—Ä–æ–ª—å"
                    value={loginPassword}
                    onChange={(e) => setLoginPassword(e.target.value)}
                    style={inputStyle}
                    required
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    style={{
                      position: 'absolute',
                      right: '10px',
                      top: '50%',
                      transform: 'translateY(-50%)',
                      background: 'none',
                      border: 'none',
                      cursor: 'pointer',
                      color: '#666'
                    }}
                  >
                    {showPassword ? 'üôà' : 'üëÅÔ∏è'}
                  </button>
                </div>
              </div>
              <button type="submit" style={submitButtonStyle}>
                üîê –í–æ–π—Ç–∏
              </button>
              <div style={{ textAlign: 'center', marginTop: '15px' }}>
                <button 
                  type="button" 
                  onClick={() => setShowLogin(false)}
                  style={{
                    background: 'none',
                    border: 'none',
                    color: '#08572f',
                    textDecoration: 'underline',
                    cursor: 'pointer',
                    fontSize: '14px'
                  }}
                >
                  –ù–µ—Ç –∞–∫–∫–∞—É–Ω—Ç–∞? –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
                </button>
              </div>
            </form>
          ) : (
            // –§–æ—Ä–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏
            <form onSubmit={handleRegister} style={formStyle}>
              <div style={inputGroupStyle}>
                <input
                  type="text"
                  placeholder="–ò–º—è"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  style={inputStyle}
                  required
                />
              </div>
              <div style={inputGroupStyle}>
                <input
                  type="email"
                  placeholder="Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  style={inputStyle}
                  required
                />
              </div>
              <div style={inputGroupStyle}>
                <div style={{ position: 'relative' }}>
                  <input
                    type={showPassword ? "text" : "password"}
                    placeholder="–ü–∞—Ä–æ–ª—å (–º–∏–Ω–∏–º—É–º 6 —Å–∏–º–≤–æ–ª–æ–≤)"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    style={inputStyle}
                    required
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    style={{
                      position: 'absolute',
                      right: '10px',
                      top: '50%',
                      transform: 'translateY(-50%)',
                      background: 'none',
                      border: 'none',
                      cursor: 'pointer',
                      color: '#666'
                    }}
                  >
                    {showPassword ? 'üôà' : 'üëÅÔ∏è'}
                  </button>
                </div>
              </div>
              <div style={inputGroupStyle}>
                <input
                  type={showPassword ? "text" : "password"}
                  placeholder="–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  style={inputStyle}
                  required
                />
              </div>
              <button type="submit" style={submitButtonStyle}>
                üìù –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
              </button>
              <div style={{ textAlign: 'center', marginTop: '15px' }}>
                <button 
                  type="button" 
                  onClick={() => setShowLogin(true)}
                  style={{
                    background: 'none',
                    border: 'none',
                    color: '#08572f',
                    textDecoration: 'underline',
                    cursor: 'pointer',
                    fontSize: '14px'
                  }}
                >
                  –£–∂–µ –µ—Å—Ç—å –∞–∫–∫–∞—É–Ω—Ç? –í–æ–π—Ç–∏
                </button>
              </div>
            </form>
          )}

          {/* –ë—ã—Å—Ç—Ä—ã–µ —Å—Å—ã–ª–∫–∏ */}
          <div style={existingUsersStyle}>
            <p>–ë—ã—Å—Ç—Ä—ã–π –¥–æ—Å—Ç—É–ø:</p>
            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
              <Link href="/blog" style={{
                ...dashboardLinkStyle,
                backgroundColor: '#4a6fa5'
              }}>
                üìù –ë–ª–æ–≥ —Å–æ–æ–±—â–µ—Å—Ç–≤–∞
              </Link>
              {currentUser && (
                <Link href="/dashboard" style={dashboardLinkStyle}>
                  üéâ –õ–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç
                </Link>
              )}
            </div>
          </div>
        </div>

        {/* –°–ø–∏—Å–æ–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π (—Ç–æ–ª—å–∫–æ –¥–ª—è –∞–¥–º–∏–Ω–∞/–≤—Å–µ—Ö) */}
        <div style={dataContainerStyle}>
          <h2 style={{ textAlign: 'center', color: '#08572f', marginBottom: '20px' }}>
            –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ —Å–∏—Å—Ç–µ–º—ã ({users.length})
          </h2>
          
          {isLoading ? (
            <p style={{ textAlign: 'center', color: '#08572f' }}>–ó–∞–≥—Ä—É–∑–∫–∞...</p>
          ) : users.length === 0 ? (
            <p style={{ textAlign: 'center', color: '#08572f' }}>–ù–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π</p>
          ) : (
            <div style={usersListStyle}>
              {users.map(user => (
                <div key={user.id} style={userCardStyle}>
                  <div style={userInfoStyle}>
                    <strong style={{ color: '#08572f' }}>{user.name}</strong>
                    <span style={{ color: '#0b311f', fontSize: '14px' }}>{user.email}</span>
                    <small style={{ color: '#666', fontSize: '12px' }}>
                      {new Date(user.created_at).toLocaleDateString('ru-RU')}
                    </small>
                  </div>
                  <button 
                    onClick={() => handleDeleteUser(user.id)}
                    style={deleteButtonStyle}
                    disabled={currentUser?.id === user.id}
                    title={currentUser?.id === user.id ? "–ù–µ–ª—å–∑—è —É–¥–∞–ª–∏—Ç—å —Å–µ–±—è" : ""}
                  >
                    –£–¥–∞–ª–∏—Ç—å
                  </button>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ —Å–∏—Å—Ç–µ–º–µ */}
        <div style={{
          maxWidth: '500px',
          margin: '40px auto',
          padding: '20px',
          background: 'rgba(8, 87, 47, 0.1)',
          borderRadius: '10px',
          textAlign: 'center'
        }}>
          <h3 style={{ color: '#08572f' }}>üîê –ë–µ–∑–æ–ø–∞—Å–Ω–∞—è —Å–∏—Å—Ç–µ–º–∞</h3>
          <p>–ò—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –±–µ–∑–æ–ø–∞—Å–Ω–æ–µ —Ö—Ä–∞–Ω–µ–Ω–∏–µ –ø–∞—Ä–æ–ª–µ–π —á–µ—Ä–µ–∑ Supabase Auth</p>
          <ul style={{ textAlign: 'left', display: 'inline-block', margin: '10px 0' }}>
            <li>–•—ç—à–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–∞—Ä–æ–ª–µ–π</li>
            <li>JWT —Ç–æ–∫–µ–Ω—ã</li>
            <li>–°–µ—Å—Å–∏–∏</li>
            <li>–í–∞–ª–∏–¥–∞—Ü–∏—è –¥–∞–Ω–Ω—ã—Ö</li>
          </ul>
        </div>

      </main>
      <footer className="footer">
        –°–∏—Å—Ç–µ–º–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ —Å –ø–∞—Ä–æ–ª—è–º–∏ ‚Ä¢ Supabase Auth
      </footer>
    </>
  );
}