import { supabase } from './supabase';

export interface User {
  id: string
  name: string
  email: string
  created_at: string
}

export const userService = {
  // –ü–æ–ª—É—á–∏—Ç—å –≤—Å–µ—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π
  async getUsers(): Promise<User[]> {
    try {
      const { data, error } = await supabase
        .from('users')
        .select('*')
        .order('created_at', { ascending: false })
      
      if (error) {
        console.error('Error fetching users:', error)
        return []
      }
      
      return data as User[]
    } catch (err) {
      console.error('Unexpected error:', err)
      return []
    }
  },
  
  // –°–æ–∑–¥–∞—Ç—å –Ω–æ–≤–æ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  async createUser(name: string, email: string): Promise<User | null> {
    try {
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —É–∂–µ —Ç–∞–∫–æ–π email
      const { data: existingUser } = await supabase
        .from('users')
        .select('*')
        .eq('email', email)
        .single()
      
      if (existingUser) {
        alert('–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Å —Ç–∞–∫–∏–º email —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç!')
        return null
      }
      
      const { data, error } = await supabase
        .from('users')
        .insert([
          { 
            name: name.trim(),
            email: email.trim()
          }
        ])
        .select()
        .single()
      
      if (error) {
        console.error('Error creating user:', error)
        alert('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è')
        return null
      }
      
      return data as User
    } catch (err) {
      console.error('Unexpected error:', err)
      return null
    }
  },
  
  // –£–¥–∞–ª–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
  async deleteUser(id: string): Promise<boolean> {
    try {
      const { error } = await supabase
        .from('users')
        .delete()
        .eq('id', id)
      
      if (error) {
        console.error('Error deleting user:', error)
        return false
      }
      
      return true
    } catch (err) {
      console.error('Unexpected error:', err)
      return false
    }
  },
  
  // –í–æ–π—Ç–∏ (–ø—Ä–æ–≤–µ—Ä–∏—Ç—å —Å—É—â–µ—Å—Ç–≤–æ–≤–∞–Ω–∏–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è)
  async login(email: string): Promise<User | null> {
    try {
      const { data, error } = await supabase
        .from('users')
        .select('*')
        .eq('email', email.trim())
        .single()
      
      if (error) {
        console.error('Error logging in:', error)
        return null
      }
      
      return data as User
    } catch (err) {
      console.error('Unexpected error:', err)
      return null
    }
  }
}